function openMenu() {
	new TimelineMax({
		onStart() {
			$('.mobile-panel').removeClass('is-hidden');
		},
		onComplete() {
			$('body').addClass('is-menu-open');
		},
	})
		.from('.mobile-panel', 0.5, {
			x: '100%',
			ease: Power1.easeInOut,
			clearProps: 'all',
		});
}

function closeMenu() {
	new TimelineMax({
		onStart() {
			$('body').removeClass('is-menu-open');
		},
		onComplete() {
			$('.mobile-panel').addClass('is-hidden');
		},
	})
		.to('.mobile-panel', 0.5, {
			x: '-100%',
			autoAlpha: 0,
			ease: Power1.easeInOut,
			clearProps: 'all',
		});
}

function openDropdownSelect($dropdown, $select) {
	new TimelineMax({
		onStart() {
			$('.select-item').removeClass('is-open-select');
			$('.select-item__dropdown').addClass('is-hidden');
			$dropdown.removeClass('is-hidden');
			$select.addClass('is-open-select');
		},
	})
		.from($dropdown, 0.3, {
			autoAlpha: 0,
			clearProps: 'all',
		});
}

function closeDropdownSelect() {
	new TimelineMax({
		onStart() {
			$('.select-item').removeClass('is-open-select');
		},
		onComplete() {
			$('.select-item__dropdown').addClass('is-hidden');
		},
	})
		.to($('.select-item__dropdown'), 0.3, {
			autoAlpha: 0,
			clearProps: 'all',
		});
}

$('.slider-block').slick({
	dots: true,
	infinite: true,
	arrows: false,
	speed: 300,
	slidesToShow: 1,
});

$('.header__burger').on('click', () => {
	openMenu();
});

$('.mobile-panel__back').on('click', () => {
	closeMenu();
});

$('.select-item__button').on('click', (e) => {
	let $this = $(e.currentTarget);

	let $select = $this.closest('.select-item');

	let $dropdown = $this.next();

	if (!$select.hasClass('is-open-select')) {
		openDropdownSelect($dropdown, $select);
	} else {
		closeDropdownSelect();
	}
});

$('.select-item__button-dropdown').on('click', (e) => {
	let $this = $(e.currentTarget);

	let $input = $this.closest('.select-item').find('input');

	let $selectButton = $this.closest('.select-item').find('.select-item__button');

	let text = $this.text();

	$selectButton.text(text);
	$input.val(text);
	closeDropdownSelect();
});

$('.mobile-panel__item-nav').on('click', (e) => {
	e.preventDefault();

	let $this = $(e.currentTarget);

	new TimelineMax({
		onStart() {
			$('body').removeClass('is-menu-open');
		},
		onComplete() {
			$('.mobile-panel').addClass('is-hidden');
			$('html, body').stop().animate({
				scrollTop: $($this.attr('href')).offset().top,
			}, 800);
		},
	})
		.to('.mobile-panel', 0.5, {
			x: '-100%',
			autoAlpha: 0,
			ease: Power1.easeInOut,
			clearProps: 'all',
		});
});

$('.datepicker').datepicker({
	minDate: 0,
});

$('.datepicker').datepicker('setDate', new Date());
